@import url('https://fonts.googleapis.com/css?family=Baloo+2:400,500,600,700,800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Baloo+2:wght@400;500;600;700;800&family=Baloo+Bhaina+2:wght@400;500;600;700;800&display=swap');
@import url('https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap');
@import url('https://fonts.googleapis.com/css?family=Roboto&display=swap');


$main-color: #004551;
$color2: #0ca890;
$background-color2: #f2f2f2;
$gradient: linear-gradient(to right, $main-color, $color2);
$fontColor: #222222;
$fontColor2: #474747;
$maxWidth: 1200px;

//elements
.App {
  color: $fontColor;
//  font-family: 'Baloo 2', sans-serif;
  font-family: 'Baloo Bhaina 2', sans-serif;
  line-height: 125%;
}

.page {
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  text-align: center;
  box-sizing: border-box;
  padding-bottom: 50px;
}

.page-title {
  padding-top: 100px;
  font-weight: bold;
  font-size: 60px;
  text-align: left;
}

.container {
  box-sizing: border-box;
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  max-width: $maxWidth;
  margin-left: auto;
  margin-right: auto;
}

h1 {
  text-align: center;
  padding: 20px;
  font-size: 2.5em;
  margin: 0px;
}

a {
  text-decoration: none;
  all: inherit;
  cursor: pointer;

  &:active {
    transform: scale(.95)
  }
}

.mobile {
  display: none;
}

//Navbar
.navbar {
  position: absolute;
  width: 100%;
  height: 80px;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
  box-sizing: border-box;
  padding-left: 20px;
  padding-right: 20px;
}

//Header image
.image-header {
  display: flex;
  align-items: center;
  justify-content: start;
  background: transparent;
  width: 200px;
  padding: 0px;
  color: white;
  margin-left: -10px;

  &:hover {
    cursor: pointer;
  }

  &:hover .mike-image {
    filter: brightness(.7);
  }

  &:hover .name {
    filter: brightness(.7);
  }
}

.name {
  text-align: left;
  font-size: 12px;
  font-weight: 700;
}

.mike-image {
  width: 70px;
  height: 70px;
  clip-path: circle(35%);
}

//Contact button
.contact-label-container {
  height: 100%;
  width: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
}

.contact-label {
  width: 80px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0px solid transparent;
  border-radius: 20px;
  font-weight: 600;
  color: $color2;
  background-color: white;
  transition: color .2s, background-color .2s, border .1s ease-out;
  right: 20px;

  &:hover{
    background: $color2;
    color: white;
    border: 2px solid white;
    // box-shadow: 0 0 2px black;
    cursor: pointer;
  }
}

//title page
#title-page {
  padding-bottom: 0px;
  position: relative;
  //  background: $main-color;
  min-height: 100vh;
  height: 100%;
  width: 100vw;
  margin-bottom: 100px;
}

//test
.skew-container {
  position: absolute;
  height: 100vh;
  width: 100vw;
}

.skewed {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to left, $main-color, $color2);
  z-index: 0;
}

.skewed2 {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  height: 90%;
  background: linear-gradient(to right, $main-color, $color2);
  transform: skewY(10deg);
  z-index: 0;
}

.svg-img {
  max-width: 500px;
  width: 100%;
}

.title-container {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 1600px;
  min-height: 100vh;
  margin-left: auto;
  margin-right: auto;
  box-sizing: border-box;
  padding: 20px;
  z-index: 100;
}

.title-left {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: 20px;
  position: relative;
}

.title-right {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: 20px;
}

.hello-container {
  position: absolute;
  top: 5%;
  left: 50%
}

.hello-bubble {
  background-color: $background-color2;
  background: white;
  width: 15vw;
  min-width: 200px;
  max-width: 300px;
  height: 50px;
  border-radius: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 5
}

.hello-bubble-text {
  font-size: 20px;
  font-weight: bold;
  color: $color2;
  margin-top: 5px;
  z-index: 6
}

.hello-triangle {
  width: 50px;
  height: 50px;
  background-color: white;
  transform: rotate(225deg);
  clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
  position: absolute;
  top: 50%;
  left: -20px;
  z-index: 4;
}

.mybg-subheader {
  width: 100%;
  box-sizing: border-box;
  padding: 0px 20px 40px 20px;
  margin-top: 5%;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.mybg-wrapper {
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding: 20px;
  box-sizing: border-box;
  margin-bottom: 20px;
}

.title-bar {
  width: 100%;
  height: 2px;
  background-color: $background-color2;
  margin: 5px;
  z-index: 10;
}

.mybg-header {
  text-align: left;
  color: white;
  font-weight: bold;
  margin-right: auto;
  font-size: 24px;
  z-index: 10;
}

.mybg-card {
  background-color: $background-color2;
  width: 45%;
  min-width: 150px;
  min-height: 200px;
  padding: 10px;
  box-sizing: border-box;
  text-align: left;
  margin: 10px;
  border-radius: 20px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.16);
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  position: relative;
  overflow: hidden;


  .mybg-title {
    font-weight: 700;
    font-size: 25px;
    color: $main-color;
    padding: 10px;
    position: absolute;
  }
  
  .mybg-icon {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto auto;
    width: 40%;
    height: 40%;
    filter: invert(100%) brightness(.05) sepia(20) saturate(80) hue-rotate(540deg);
    transform: scale(1);
    transition: transform .4s;
  }
  
  .mybg-text {
    box-sizing: border-box;
    padding: 10px;
    font-size: 10px;
    transition: opacity .4s transform .4s;
    text-align: center;
    position: absolute;
    top: 100%;
    transform: translateY(0px);
    opacity: 0;
    transition: opacity .4s, transform .2s;
  }

  &:hover {
    .mybg-text {
      transform: translateY(-100%);
      opacity: 1;
    }

    .mybg-icon {
      transform: scale(.8) translateY(-20%);
    }
  }
}



@media only screen and (max-width: 1000px) {
  .title-container {
    padding-top: 100px;
    flex-wrap: wrap
  }

  .skewed2 {
    height: 100%;
  }
}



@media only screen and (max-width: 600px) {
  #title-page {
    margin-bottom: 0px;
  }

  .mybg-subheader {
    width: 100%;
  }

  .mybg-card {
    width: 100%;
    box-shadow: none;
    margin: 0px;
  }

  .hello-bubble {
    min-width: 150px;
    height: 40px;
  }

  .hello-triangle {
    width: 40px;
    height: 40px;
    background-color: white;
    top: 50%;
    left: -15px;
    z-index: 4;
  }

  .title-right {
    padding: 0px;
  }

  .skewed2 {
    position: absolute;
    top: -50%;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 0;
    width: 100%;
    height: 150%;
  }

  .title-left {
    margin-top: 100px;
    margin-bottom: 100px;
  }

  .title-bar {
    width: 100%;
    height: 2px;
    background-color: $background-color2;
    margin: 0px;
    margin-top: 10px;
    margin-bottom: 30px;
    z-index: 10;
  }
}

//Work
.work-container {
  position: relative;
  width: 100%;
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;

  .work-bar {
    position: absolute;
    border-radius: 10px;
    margin-left: 20px;
    height: 100%;
    width: 20px;
    background: linear-gradient($main-color, $color2);
  }

  .job {
    width: 100%;
    display: flex;
    position: relative;

    .bullet {
      position: absolute;
      background-color: white;
      top: 35px;
      left: 10px;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      z-index: 100;
      display: flex;
      align-items: center;
      justify-content: center;

      .bullet-inner {
        height: 80%;
        width: 80%;
        border-radius: 50%;
        background: linear-gradient($main-color, $color2);
      }
    }

    .role {
      margin-left: 100px;
      margin-top: 50px;

      ul {
        margin: 0px;
        padding-inline-start: 20px;
      }

      .company {
        font-size: 40px;
        font-weight: bold;
      }
  
      .position {
        font-size: 16px;
        text-align: left;
        margin-left: 20px;
        flex: 1;
        font-weight: 500;
      }

      .time {
        text-align: left;
        margin-top: 10px;
        margin-bottom: 10px;
        color: gray;
      }

      .description {
        text-align: left;
        opacity: 0;
        max-height: 0;
        transition: max-height .4s, opacity .2s ease-in;
      }

      &:hover {
        .description {
          max-height: 500px;
          opacity: 1;
        }
      }
    }
  }
}

@media only screen and (max-width: 600px) {
  .work-container {
    .job {
      .role {
        margin-left: 60px;

        .company {
          font-size: 26px;
        }

        .position {
          margin-left: 10px;
          font-size: 12px;
        }
      }
    }
  }

}



//Projects
#projects {
  // background-color: $background-color2;
  text-align: left;
  padding-bottom: 40px
}

.project {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  padding-top: 50px;
  padding-bottom: 50px;
}

.project2 {
  display: flex;
  flex-wrap: wrap-reverse;
}

.project-description-box {
  width: 100%;
  max-width: 400px;
}

.project-title{
  font-weight: 700;
  font-size: 40px;
}

.project-subheader {
  font-weight: 200;
  font-size: 12px;
  margin-top: 2px;
  margin-bottom: 10px;
  color: gray;
}

.project-description {
  padding-top: 5px;
  padding-bottom: 10px;
  color: $fontColor2;
}

.icon-container {
  width: 100%;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

#project-icon {
  width: 45px;
  height: 45px;
  padding: 3px;
  box-sizing: border-box;
  margin-right: 5px;
  border-radius: 10px;
  background-color: $background-color2;
}

.description-link-wrapper {
  display: flex;
  width: 100%;
  margin-right: auto;
}

